@import url(https://fonts.googleapis.com/css2?family=Ewert&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Eczar&family=Trirong&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
.AppContainer {
  margin: 0;
  padding: 0;
  background-color: #e7cbbf;
}

* {
  box-sizing: border-box;
}

.ewert {
  font-family: "Ewert", cursive;
}

.eczar {
  font-family: "Eczar", serif;
}

.tarea {
  background-color: #e6b8a2;
}

.tarea:hover {
  transform: scaleX(1.02);
}

.hidden {
  display: none;
}

.important {
  border: 1px ridge #fff1e6;
  box-shadow: 1px 1px 15px 1px #fff1e6;
}

